}

.home-link {
  position: absolute;
  top: 10px;
  left: 10px;
}

img {
  max-width: 100%;
  display: block;
}

[data-start] {
  color: #fff;
  text-transform: uppercase;
  background-color: #212121;
  border: none;
  border-radius: 4px;
  padding: 1rem;
  font-size: 20px;
  text-decoration: none;
}

[data-start]:disabled {
  background-color: gray;
}

[data-stop] {
  color: #fff;
  text-transform: uppercase;
